/**
 * Alert
 * 
 * chooseclear
 */

import React, { Component } from 'react';
import {Alert} from 'antd'
import './index.less'

class AlertCom extends Component {
    render() {
        const link=<a onClick={this.props.clear}></a>
        return (
            <Alert message={''+this.props.choose+''} description={this.props.choose?link:''} type="info" showIcon />
        );
    }
}

export default AlertCom;
