import React, { Component } from 'react';
import { Drawer, Form, Button, Col, Row, Input, Select, DatePicker, Card,Radio} from 'antd';
import TableList from '../../component/case/tablelist'
const { Option } = Select;
const { TextArea } = Input;
class ModalLeft extends Component {
    constructor() {
        super()
        this.state = {
            visible: false
        }
    }
    //
    close = () => {
        this.props.visibleclose()
    }
    render() {
        const { getFieldDecorator } = this.props.form
        return (
            <div>
                <Drawer
                    title=""
                    width={1000}
                    onClose={this.close}
                    visible={this.props.visible}
                >

                    <Form layout="vertical" hideRequiredMark>
                        <Card title=''>
                            <Row gutter={16}>
                                <Col span={8}>
                                    <Form.Item label="">
                                        {getFieldDecorator('name', {
                                            rules: [{ required: true, message: '' }],
                                        })(<Input placeholder="" />)}
                                    </Form.Item>
                                </Col>
                                <Col span={8}>
                                    <Form.Item label="">
                                        {getFieldDecorator('number', {
                                            rules: [{ required: true, message: '' }],
                                        })(<Input placeholder="" />)}
                                    </Form.Item>
                                </Col>
                                <Col span={8}>
                                    <Form.Item label="">
                                        {getFieldDecorator('usernumber', {
                                            rules: [{ required: true, message: '' }],
                                        })(<Input placeholder="" />)}
                                    </Form.Item>
                                </Col>
                            </Row>
                        </Card>
                        <Card title="">
                            <Row justify="space-between" type="flex" gutter={16}>
                                <Col span={5}>
                                    <Form.Item label="">
                                        {getFieldDecorator('caseNumber', {
                                            rules: [{ required: true, message: '' }],
                                        })(<Input placeholder="" />)}

                                    </Form.Item>
                                </Col>
                                <Col span={5}>
                                    <Form.Item label="">
                                        {getFieldDecorator('caseName', {
                                            rules: [{ required: true, message: '' }],
                                        })(<Input placeholder="" />)}

                                    </Form.Item>
                              
                                </Col>
                                <Col span={5}>
                                    <Form.Item label="">
                                        {getFieldDecorator('allfee', {
                                            rules: [{ required: true, message: '' }],
                                        })(<Input placeholder="" />)}

                                    </Form.Item>
                                </Col>
                                <Col span={5}>
                                    <Form.Item label="">
                                        {getFieldDecorator('status', {
                                            rules: [{ required: true, message: '' }],
                                        })(<Radio.Group>
                                            <Radio value="1"></Radio>
                                            <Radio value="2"></Radio>
                                        </Radio.Group>)}
                                           
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row justify="space-between" type="flex" gutter={16}>
                                <Col span={5}>
                                    <Form.Item label="">
                                        {getFieldDecorator('prosecutor', {
                                            rules: [{ required: true, message: '' }],
                                        })(<Input placeholder="" />)}

                                    </Form.Item>
                                </Col>
                                <Col span={5}>
                                    <Form.Item label="">
                                        {getFieldDecorator('byprosecutor', {
                                            rules: [{ required: true, message: '' }],
                                        })(<Input placeholder="" />)}

                                    </Form.Item>
                                </Col>
                                <Col span={5}>
                                    <Form.Item label="">
                                        {getFieldDecorator('lawyer', {
                                            rules: [{ required: true, message: '' }],
                                        })(<Input placeholder="" />)}
                                    </Form.Item>
                                </Col>
                                <Col span={5}>
                                    <Form.Item label="">
                                       {getFieldDecorator('courtDay', {
                                            rules: [{ required: true, message: '' }],
                                        })(<Input placeholder="" />)}
                                           
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row justify="space-between" type="flex" gutter={16}>
                                <Col span={5}>
                                    <Form.Item label="">
                                        {getFieldDecorator('closeTime', {
                                            rules: [{ required: true, message: '' }],
                                        })(<Input placeholder="" />)}

                                    </Form.Item>
                                </Col>
                                <Col span={5}>
                                    <Form.Item label="">
                                        {getFieldDecorator('ticket', {
                                            rules: [{ required: true, message: '' }],
                                        })(<Input placeholder="" />)}

                                    </Form.Item>
                                </Col>
                                <Col span={5}>
                                    <Form.Item label="">
                                        {getFieldDecorator('delegateType', {
                                            rules: [{ required: true, message: '' }],
                                        })(<Input placeholder="" />)}
                                    </Form.Item>
                                </Col>
                                <Col span={5}>
                                    <Form.Item label="">
                                       {getFieldDecorator('sueType', {
                                            rules: [{ required: true, message: '' }],
                                        })(<Input placeholder="" />)}
                                           
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row justify="space-between" type="flex" gutter={16}>
                                <Col span={5}>
                                    <Form.Item label="">
                                        {getFieldDecorator('caseType', {
                                            rules: [{ required: true, message: '' }],
                                        })(<Input placeholder="" />)}

                                    </Form.Item>
                                </Col>
                                <Col span={5}>
                                   
                                </Col>
                                <Col span={5}>
                                   
                                </Col>
                                <Col span={5}>
                                   
                                </Col>
                            </Row>
                            <Row>
                              <Form.Item label="">
                              {getFieldDecorator('caseDesc')(
                                   <TextArea rows={4} placeholder=""/>
                              )}
                              </Form.Item>
                            </Row>
                        </Card>
                    </Form>
                    <Card title="">
                        <TableList></TableList>
                    </Card>
                    <div
                        style={{
                            position: 'absolute',
                            left: 0,
                            bottom: 0,
                            width: '100%',
                            borderTop: '1px solid #e9e9e9',
                            padding: '10px 16px',
                            background: '#fff',
                            textAlign: 'right',
                        }}
                    >
                        <Button onClick={this.close} style={{ marginRight: 8 }}>
                            
            </Button>
                        <Button onClick={this.onClose} type="primary">
                            
            </Button>
                    </div>
                </Drawer>
            </div>
        );
    }
}
ModalLeft = Form.create()(ModalLeft);
export default ModalLeft;


