import React from 'react'
import { Tabs, Radio,Card } from 'antd';
const TabPane = Tabs.TabPane;
export default class Tabss extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
          mode: 'top',
        };
      }
    
      handleModeChange = (e) => {
        const mode = e.target.value;
        this.setState({ mode });
      }
    render(){
        const { mode } = this.state;
        return(
            <div>
                 <Card style={{marginBottom:20}} title="Tabs" >
                 <Radio.Group onChange={this.handleModeChange} value={mode} style={{ marginBottom: 8 }}>
                 <Radio.Button value="top"></Radio.Button>
                <Radio.Button value="left"></Radio.Button>
        </Radio.Group>
        <Tabs
          defaultActiveKey="1"
          tabPosition={mode}
          style={{ height: 220 }}
        >
          <TabPane tab="Tab 1" key="1"> 1 </TabPane>
          <TabPane tab="Tab 2" key="2"> 2 </TabPane>
          <TabPane tab="Tab 3" key="3"> 3 </TabPane>
          <TabPane tab="Tab 4" key="4"> 4 </TabPane>
          <TabPane tab="Tab 5" key="5"> 5 </TabPane>
          <TabPane tab="Tab 6" key="6"> 6 </TabPane>
          <TabPane tab="Tab 7" key="7"> 7 </TabPane>
          <TabPane tab="Tab 8" key="8"> 8 </TabPane>
          <TabPane tab="Tab 9" key="9"> 9 </TabPane>
          <TabPane tab="Tab 10" key="10"> 1 0</TabPane>
          <TabPane tab="Tab 11" key="11"> 1 1</TabPane>
        </Tabs>
                </Card>
                
            </div>
        )
    }
}