import React from 'react'
import { Button, notification,Card } from 'antd';
export default class Notifications extends React.Component{
    state={
        key:'updatable'
    }
    openNotificationWithIcon = (type) => {
        notification[type]({
          message: 'Notification Title',
          description: type,
        });
      }
      

       openNotification = () => {
        notification.open({
          key:this.state.key,
          message: 'Notification Title',
          description: 'description.',
        });
        setTimeout(() => {
          notification.open({
            key:this.state.key,
            message: '',
            description: '',
          });
        }, 3000);
      }      
    render(){
        return(
            <div>
                <Card style={{marginBottom:20}} title="" >
                <Button onClick={() => this.openNotificationWithIcon('success')}></Button>
                <Button onClick={() => this.openNotificationWithIcon('info')}></Button>
                <Button onClick={() => this.openNotificationWithIcon('warning')}></Button>
                <Button onClick={() => this.openNotificationWithIcon('error')}></Button>
                <Button type="primary" onClick={this.openNotification}>key</Button>
                </Card>
            </div>
        )
    }
}