import React from 'react'
import {Form,Select,DatePicker,message,Button} from 'antd'
const FormItem=Form.Item
const Option=Select.Option
export default class FilterFrom extends React.Component{
    submitOrder=()=>{
        this.props.form.validateFields((err, values) => {
           err?message.error(err):this.props.orderfn(values)
          });
    }
    render(){
        const { getFieldDecorator } = this.props.form;
        return(
            <div>
                <Form layout='inline'>
                <FormItem label="">
                {getFieldDecorator('city_id')(
                <Select style={{width:100}}  placeholder="">
                  <Option value=""></Option>
                  <Option value="1"></Option>
                  <Option value="2"></Option>
                  <Option value="3"></Option>
                </Select>
                    )}
                        </FormItem>
                        <FormItem label="">
                        {getFieldDecorator('start-time')(
                            <DatePicker showTime format="YYYY-MM-DD HH:mm:ss" />
                        )}      
                       
                        </FormItem>
                        <FormItem>
                            
                        {getFieldDecorator('end-time')(
                         <DatePicker showTime format="YYYY-MM-DD HH:mm:ss" />
                        )}
                        </FormItem>
                        <FormItem label="">
                {getFieldDecorator('op_mode')(
                <Select style={{width:100}}  placeholder=""> 
                  <Option value=""></Option>
                  <Option value="1"></Option>
                  <Option value="2"></Option>
                  <Option value="3"></Option>
                </Select>
                    )}
                        </FormItem>
                        <FormItem>
                            <Button onClick={this.submitOrder}></Button>
                            <Button onClick={()=>{
                                this.props.form.resetFields();
                            }}></Button>
                        </FormItem>
                </Form>
            </div>
        )
    }
}
FilterFrom=Form.create({})(FilterFrom);