import React from 'react'
import {Button,Card,Icon,Radio} from 'antd'
import './index.less'
const ButtonGroup = Button.Group;
export default class Buttons extends React.Component{
    state={
        loading:true,
        size:'small'
    }
    handcoseloading(){
        
        this.setState({
            loading:!this.state.loading
        })
    }
    handleSizeChange=(e)=>{
           this.setState({
               size:e.target.value
           })
    }
    render(){
        return(
            <div>
                <Card style={{marginBottom:20}} title="" >
                  <Button type="primary">kai antd</Button>
                  <Button>kai antd</Button>
                  <Button type="dashed">kai antd</Button>
                  <Button type="danger">kai antd</Button>
                </Card>
                <Card style={{marginBottom:20}} title=""  >
                  <Button icon="plus"></Button>
                  <Button icon="edit"></Button>
                  <Button icon="delete"></Button>
                  <Button icon="search" shape="circle"></Button>
                  <Button type="primary" icon="download"></Button>
                  <Button type="primary" icon="search"></Button>
                </Card>
                <Card style={{marginBottom:20}} title="Loading"  >
                  <Button type="primary" loading={this.state.loading} ></Button>
                  <Button type="primary" shape="circle" loading={this.state.loading} ></Button>
                  <Button loading={this.state.loading}></Button>
                  <Button shape="circle" loading={this.state.loading}></Button>
                  <Button onClick={this.handcoseloading.bind(this)}>{this.state.loading?'':''}</Button>
                </Card>
                <Card style={{marginBottom:20}} title="" >
                <ButtonGroup>
                     <Button type="primary" style={{margin:0}}>
                     <Icon type="left" />
                     </Button>
                     <Button type="primary" style={{margin:0}}>
                         <Icon type="right" />
                     </Button>
                </ButtonGroup> 
                </Card>
                <Card style={{marginBottom:20}} title="" >
                 <Radio.Group value={this.state.size} onChange={this.handleSizeChange}>
                     <Radio value="large"></Radio>
                     <Radio value="default"></Radio>
                     <Radio value="small"></Radio>
                 </Radio.Group>
                 <Button size={this.state.size} type="primary">kai antd</Button>
                  <Button size={this.state.size}>kai antd</Button>
                  <Button size={this.state.size} type="dashed">kai antd</Button>
                  <Button size={this.state.size} type="danger">kai antd</Button>
                </Card>
            </div>
        )
    }
}