import React from 'react'
import { message, Button,Card } from 'antd';
export default class Messages extends React.Component{
    toggle=()=>{
        message.info('This is a ');
    }
    success = () => {
        message.success('This is a message of ',10);
      }
      
    error = () => {
        message.error('This is a message of ');
      }
      
    warning = () => {
        message.warning('This is message of ');
      }
    render(){
        return(
            <div>
                <Card style={{marginBottom:20}} title="" >
                 <Button onClick={this.toggle}  type="primary">
                    
                 </Button>
                 <Button onClick={this.success}>10s</Button>
                 <Button onClick={this.error}></Button>
                 <Button onClick={this.warning}></Button>
                </Card>
            </div>
        )
    }
}