import React, { Component } from 'react';
import { Modal, Card, Button, Row, Col, Form, Input, Radio, Switch, Upload, Icon } from 'antd'
const FormItem = Form.Item;
const { TextArea } = Input;
class DrawModal extends Component {
    render() {
        return (
            <div>
                <Modal
                    visible={this.props.visible}
                    title=""
                    width={1000}
                    onOk={this.handleOk}
                    onCancel={() => { this.props.handleCancel() }}
                    footer={[
                        <Button key="back" onClick={() => { this.props.handleCancel() }}>
                            
                        </Button>,
                        <Button key="submit" type="primary" loading={this.props.loading} onClick={this.handleOk}>
                            
                        </Button>,
                    ]}
                >
                    <Card title="">
                        <Row gutter={14}>
                            <Col span={8}>
                                <strong>:</strong><span></span>
                            </Col>
                            <Col span={8}>
                                <strong>:</strong><span>13981722057</span>
                            </Col>
                        </Row>
                        <Row gutter={14}>
                            <Col span={8}>
                                <strong>:</strong><span></span>
                            </Col>
                            <Col span={8}>
                                <strong>:</strong><span></span>
                            </Col>
                            <Col span={8}>
                                <strong>:</strong><span></span>
                            </Col>
                        </Row>
                        <Row gutter={14}>
                            <Col span={24}>
                                <strong>:</strong><span></span>
                            </Col>
                        </Row>
                    </Card>
                    <Card title="">
                        <DrawTop />
                    </Card>
                    <Card title="">
                        <DrawCenter />
                    </Card>
                    <Card title="">
                        <DrawBottom />
                    </Card>
                </Modal>
            </div>
        );
    }
}

export default DrawModal;




//
class DrawTop extends Component {
    submits=()=>{
      console.log(this.props.form.getFieldsValue())
    }
    render() {
        const layoutItem = {
            labelCol: {
                span: 4
            },
            wrapperCol: {
                span: 16
            }
        }
        const { getFieldDecorator } = this.props.form;
        return (
            <div>
                <Form layout="vertical">
                    <Row>
                        <FormItem {...layoutItem} label=''>
                            {
                                getFieldDecorator('textare')(
                                    <TextArea rows={4} />
                                )
                            }
                        </FormItem>
                    </Row>
                    <Row type="flex" justify="end">
                        <Col push={8} span={20}>
                            <FormItem>
                                {getFieldDecorator('radio-group')(
                                    <Radio.Group>
                                        <Radio value="a">item 1</Radio>
                                        <Radio value="b">item 2</Radio>
                                        <Radio value="c">item 3</Radio>
                                    </Radio.Group>
                                )}
                            </FormItem>
                        </Col>
                        <Col push={1} span={4}>
                            <FormItem>
                                <Button onClick={this.submits}></Button>
                            </FormItem>
                        </Col>


                    </Row>
                </Form>
            </div>
        );
    }
}
DrawTop = Form.create({ name: 'yuanyin' })(DrawTop);


//
class DrawCenter extends Component {
    render() {
        const layoutItem = {
            labelCol: {
                span: 8
            },
            wrapperCol: {
                span: 16
            }
        }
        const { getFieldDecorator } = this.props.form;
        return (
            <div>
                <Form layout="inline">
                    <Row>
                        <FormItem label=''>
                            {getFieldDecorator('isout', { valuePropName: 'checked' })(<Switch />)}
                        </FormItem>
                        <FormItem {...layoutItem} label="">
                            {getFieldDecorator('out')(
                                <Input placeholder="" />
                            )}
                        </FormItem>
                        <FormItem {...layoutItem} label="">
                            {getFieldDecorator('in')(
                                <Input placeholder="" />
                            )}
                        </FormItem>
                    </Row>
                    <Row type="flex" justify="end">
                        <FormItem>
                            <Button></Button>
                        </FormItem>
                    </Row>
                </Form>
            </div>
        );
    }
}
DrawCenter = Form.create({ name: 'yuanyins' })(DrawCenter);

