import React from 'react'
import { Form, Select,Button} from 'antd'
const FormItem = Form.Item
const Option=Select.Option
export default class FilterForm extends React.Component {
    submits=(e)=>{
        this.props.form.validateFields((err, values) => {
            this.props.subMit(values)
        });
      
    }
    render() {
        const { getFieldDecorator } = this.props.form;
        return (
            <Form layout="inline">
                <FormItem label="" >
                    {getFieldDecorator('city_id')(
                        <Select style={{width:'100px'}} placeholder="">
                            <Option value=""></Option>
                            <Option value="1"></Option>
                            <Option value="2"></Option>
                            <Option value="3"></Option>
                        </Select>
                    )}
                </FormItem>
                <FormItem label="" >
                    {getFieldDecorator('mode')(
                        <Select style={{width:'120px'}} placeholder="">
                            <Option value=""></Option>
                            <Option value="1"></Option>
                            <Option value="2"></Option>
                        </Select>
                    )}
                </FormItem>
                <FormItem label="" >
                    {getFieldDecorator('ops_mode')(
                        <Select style={{width:'100px'}} placeholder="">
                            <Option value=""></Option>
                            <Option value="1"></Option>
                            <Option value="2"></Option>
                        </Select>
                    )}
                </FormItem>
                <FormItem label="" >
                    {getFieldDecorator('op_mode')(
                        <Select style={{width:'100px'}} placeholder="" width='100px'>
                            <Option value=""></Option>
                            <Option value="1"></Option>
                            <Option value="2"></Option>
                        </Select>
                    )}
                </FormItem>
                <FormItem>
                    <Button type="primary" style={{margin:'0 20px'}} onClick={this.submits}></Button>
                    <Button onClick={()=>{
                        this.props.form.resetFields();
                    }}></Button>
                </FormItem>
                
            </Form>

        )
    }
}
FilterForm=Form.create({})(FilterForm);